import vCheckbox from '../../../../components/vCheckbox'

export default {
	components: { 
		'v-checkbox': vCheckbox
	 },
	data() {
		return {
			form: {
				active: false,
				appInfo: '',
				content: '',
				mobile: ''
			}
		};
	},
	onReady() {
		this.appInfo = uni.getStorageSync('appInfo')
	},
	methods: {
		checkStatus(active) {
			this.form.active = active
		},
		submit() {
			if (!this.content.trim()) {
				uni.showToast({
					title: '请输入反馈内容',
					icon: 'none'
				})
				return
			} else if (!this.mobile.trim()) {
				uni.showToast({
					title: '请输入手机号码',
					icon: 'none'
				})
				return
			}
			let param = {
				openId: this.appInfo.openid,
				content: `${this.content};${this.mobile}`
			}
			uni.request({
				url: 'https://messagecome.com/feed/back/add',
				data: param,
				method: 'POST'
			}).then(infoRes => {
				let [err, res] = infoRes
				if (res.data && res.data.status === 1) {
					uni.showToast({
						title: '提交成功',
						duration: 2000,
						success() {
							uni.navigateBack({})
						}
					})
				}
			})
		}
	}
}
